import React from "react";
//import * as Icon from "react-bootstrap-icons";
import facebookLogo from "../icons/facebook.ico";
import youtubeLogo from "../icons/youtube.ico";
import instagramLogo from "../icons/instagram.png";
import twitterLogo from "../icons/twitter.ico";
import tiktokLogo from "../icons/tiktok_circle.png";
import gankNowLogo from "../icons/ganknow.ico";
import cosplayManiaLogo from "../icons/cosplaymania_logo.jpg";
import pinterestLogo from "../icons/pinterest.png";
//import imageLoading from "../icons/image-loading.png";

export default function SocialsSection() {
  var socialsArr = [
    // {
    //   title: "Cosplay Website",
    //   description: "Cosplay Website",
    //   link: "arjayemcosplay.netlify.app",
    //   iconType: "icon",
    //   icon: <Icon.Globe />,
    // },
    {
      title: "Facebook.com/arjayem.cosplay",
      description: "Cosplay Account",
      link: "www.facebook.com/arjayem.cosplay",
      iconType: "img",
      icon: facebookLogo,
    },
    {
      title: "Youtube.com/ArJayEmCosplay",
      description: "Cosplay Account",
      link: "www.youtube.com/channel/UCep5khamOUzX5Nu-09jkMbg",
      iconType: "img",
      icon: youtubeLogo,
    },
    {
      title: "Instagram.com/arjayem.cosplay",
      description: "Cosplay Account",
      link: "www.instagram.com/arjayem.cosplay",
      iconType: "img",
      icon: instagramLogo,
    },
    {
      title: "Twitter.com/@ArJayEmCosplay",
      description: "Cosplay Account",
      link: "www.twitter.com/@ArJayEmCosplay",
      iconType: "img",
      icon: twitterLogo,
    },
    {
      title: "Tiktok.com/@arjayem.cosplay",
      description: "Cosplay Account",
      link: "www.tiktok.com/@arjayem.cosplay",
      iconType: "img",
      icon: tiktokLogo,
    },
    {
      title: "GankNow.com/ArJayEmCosplay",
      description: "Cosplay Account",
      link: "www.ganknow.com/ArJayEmCosplay",
      iconType: "img",
      icon: gankNowLogo,
    },
    {
      title: "CosplayMania.com/arjayem_cosplay",
      description: "Cosplay Account",
      link: "www.cosplaymania.com/arjayem_cosplay",
      iconType: "img",
      icon: cosplayManiaLogo,
    },
    {
      title: "Pinterest.com/arjayemcosplay",
      description: "Cosplay Account",
      link: "www.pinterest.com/arjayemcosplay",
      iconType: "img",
      icon: pinterestLogo,
    },
  ];

  return (
    <div className="socials-container">
      {socialsArr.map((e, i) => {
        return (
          <div className="social" key={i}>
            <a
              href={"https://" + e.link}
              key={i}
              target="_blank"
              rel="noreferrer noopener"
            >
              {e.iconType === "icon" ? (
                e.icon
              ) : (
                <img className="icon" src={e.icon} alt="" />
              )}

              {e.title}
            </a>
          </div>
        );
      })}
    </div>
  );
}
