/* eslint-disable */
import React, { useState, useEffect } from "react";

import Navigation from "./components/Navigation";
import Container from "./components/Container";
import Events from "../section/EventsSection";
import Welcome from "../pages/Welcome";
import Shop from "../pages/Shop";
import SocialsSection from "../section/SocialsSection";
import AboutMeSection from "../section/AboutMeSection";

export default function Dashboard() {
  var navigationLinks = [
    { title: "Dashboard", link: "/", child: <Welcome /> },
    { title: "About Me", link: "/aboutme", child: <AboutMeSection /> },
    //{ title: "Gallery" },
    { title: "Events", link: "/events", child: <Events /> },
    { title: "Shop", link: "/shop", child: <Shop /> },
    { title: "Socials", link: "/socials", child: <SocialsSection /> },
  ];

  var scrollTimer = -1;
  function onContainerScroll() {
    if (scrollTimer != -1) clearTimeout(scrollTimer);
    scrollTimer = window.setTimeout(switchNav, 100);
  }

  function switchNav() {
    var sections = Array.from(document.querySelectorAll("section"));
    sections.forEach((section) => {
      var position = section.getBoundingClientRect();
      if (position.top >= 0 && position.bottom <= window.innerHeight) {
        //console.log(section, position);
        document.querySelectorAll("nav li a").forEach((link) => {
          link.classList.remove("active");
        });
        var id = section.getAttribute("id");
        //console.log(section, id);
        document
          .querySelector("nav li a[href='#" + id + "']")
          .classList.add("active");
        return;
      }
    });
  }

  return (
    <>
      <Navigation isDashboard="true" />
      <Container onScroll={onContainerScroll}>
        {navigationLinks.map((e, i) => {
          var id = e.title.toLocaleLowerCase().replace(/[ ]/g, "");
          var link = e.link || "/" + id;
          return (
            <section
              id={id}
              key={i}
              style={{
                backgroundImage:
                  i === 0
                    ? "url('https://firebasestorage.googleapis.com/v0/b/blogging-97ff7.appspot.com/o/dashboard%2Fbanner%200.png?alt=media&token=68d570fd-e97a-4b83-ba2a-349151723782')"
                    : "",
              }}
            >
              <h1 className="section-title">
                <a className="section-link" href={link}>
                  {e.title}
                </a>
              </h1>
              {e.child ? e.child : e.title}
            </section>
          );
        })}
      </Container>
    </>
  );
}
