/* eslint-disable */
import React, { useState, useEffect } from "react";
import { eventsCollection } from "../firebase";
import { isBrowser, isMobile } from "react-device-detect";
import * as Icon from "react-bootstrap-icons";
import imageLoading from "../icons/image-loading.png";

export default function EventsSection({ id }) {
  const [events, setEvents] = useState(() => null);
  const [addEvent, setAddEvent] = useState(() => false);
  var isLoggedIn = false;

  useEffect(
    () => {
      getEvents();
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  async function getEvents() {
    await eventsCollection
      .limit(isMobile ? 2 : 3)
      .orderBy("postedDate")
      .get()
      .then((snapshots) => {
        setEvents(() =>
          snapshots.docs.map((e) => {
            return e.data();
          })
        );
      })
      .catch((e) => {
        console.error("get-events-error", e);
      });
  }

  function onAddEvent(e) {
    e.preventDefault();
    setAddEvent(!addEvent);
    var root = document.getElementById("root");
    if (root.classList.contains("in-modal")) root.classList.remove("in-modal");
    else root.classList.add("in-modal");
  }

  function addEventModal() {
    return (
      <div id="modal">
        <div className="add-event-modal modal">
          <h1>
            <span>Add Event</span>
            <Icon.X
              className="close"
              size={35}
              title="Close"
              onClick={(e) => {
                onAddEvent(e);
              }}
            />
          </h1>
          <form>
            <div className="form-group grid-8">
              <input type="text" className="form-control" placeholder="Title" />
            </div>
            <div className="form-group grid-4">
              <input
                type="text"
                className="form-control"
                placeholder="Caption"
              />
            </div>
            <div className="form-group grid-12">
              <textarea
                name=""
                id=""
                className="form-control"
                rows="10"
              ></textarea>
            </div>
            <div className="form-group grid-4">
              <input
                type="file"
                name=""
                accept="image/*"
                id=""
                className="form-control"
                placeholder="Thumbnail"
              />
            </div>
            <div className="form-group grid-12 actions">
              <button className="btn btn-primary" title="Save">
                <Icon.Save /> Save
              </button>
            </div>
          </form>
        </div>
      </div>
    );
  }

  function viewEventSection() {
    return (
      <section className="event">
        <h1>{id}</h1>
      </section>
    );
  }

  return (
    <>
      {addEvent && addEventModal()}
      <div className={"events-container" + (id ? " view-event" : "")}>
        {/* {isMobile && (
          <div className="controls left">
            <Icon.ChevronLeft size={40} color="gold" />
          </div>
        )} */}
        {id && viewEventSection()}
        <div className="event-links">
          {events &&
            events.map((e, i) => {
              var input = Array.from(Array(e.days).keys());
              var last = input.pop() + 1;
              const days =
                input.map((e, i) => e + 1).join(", ") + " and " + last;
              return (
                <a href={"/events/" + e.id} key={i}>
                  <div className="event-card card" key={i} title="Read more...">
                    <div className="description">
                      <h2>{e.title}</h2>
                      <h5>
                        <span>{e.caption}</span>
                        {/* <span>{e.postedDate}</span> */}
                      </h5>
                      <p>{e.description}</p>
                      <strong className="read-more">Read more...</strong>
                    </div>
                    <div
                      className="thumbnail"
                      data-src={e.thumbnail}
                      style={{
                        backgroundImage:
                          "url(" +
                          (isMobile ? e.thumbnail : imageLoading) +
                          ")",
                        backgroundSize: (isMobile ? "cover" : "contain"),
                      }}
                    ></div>
                  </div>
                </a>
              );
            })}
          {events &&
            events.map((e, i) => {
              var input = Array.from(Array(e.days).keys());
              var last = input.pop() + 1;
              const days =
                input.map((e, i) => e + 1).join(", ") + " and " + last;
              return (
                <a href={"/events/" + e.id} key={i}>
                  <div className="event-card card" key={i} title="Read more...">
                    <div className="description">
                      <h2>{e.title}</h2>
                      <h5>
                        <span>{e.caption}</span>
                        {/* <span>{e.postedDate}</span> */}
                      </h5>
                      <p>{e.description}</p>
                      <strong className="read-more">Read more...</strong>
                    </div>
                    <div
                      className="thumbnail"
                      data-src={e.thumbnail}
                      style={{
                        backgroundImage:
                          "url(" +
                          //(isMobile ? e.thumbnail : imageLoading) +
                          imageLoading +
                          ")",
                          backgroundSize: (isMobile ? "contain" : "contain"),
                      }}
                    ></div>
                  </div>
                </a>
              );
            })}
        </div>
        {isLoggedIn && (
          <a
            href="#"
            className="add-card card"
            title="Add Event"
            onClick={(e) => onAddEvent(e)}
          >
            <Icon.Plus size={60} /> <span>Add Event</span>
          </a>
        )}

        {/* {isMobile && (
          <div className="controls right">
            <Icon.ChevronRight size={40} color="gold" />
          </div>
        )} */}
      </div>
    </>
  );
}
